.TodoListItem {
  padding: 0.3rem 0;
  display: flex;
  align-items: center;
  font-size: 1.1rem;

  .checkbox {
    cursor: pointer;
    flex: 1;
    display: flex;
    align-items: center;
    padding: 5px 8px;
    border: 2px dashed #f5f1e5;

    svg {
      font-size: 1.5rem;
    }
    .text {
      flex: 1;
      display: flex;
      justify-content: space-between;
      span {
        font-size: 1.1rem;
        &.date {
          color: #777777;
          font-weight: 300;
          font-size: 1rem;
        }
      }
    }
    &.checked {
      background-color: #fefff5;
      border: 2px dashed #3a4d39;
    }
    &:hover {
      background-color: #fefff5;
    }
  }
  .remove {
    display: flex;
    align-items: center;
    font-size: 1.5rem;
    color: #ff6b6b;
    cursor: pointer;
    &:hover {
      color: #ff8787;
    }
  }
}
